<template>
  <div class="movie-list">
  <ul>
    <li v-for="movie in movieList" :key="movie.id" class="movie" @click="goDetail(movie.id)">
      <div class="movie-img">
      <img :src="movie.img" alt="">
      </div>
      <div class="movie-info">
        <p class="movie-name">{{movie.nm}}</p>
        <p>{{movie.ver}}</p>
        <p>主演：{{movie.star}}</p>
        <p>{{movie.rt}}</p>
        <p>{{movie.showInfo}}</p>
      </div>
    </li>
  </ul>
    <div class="loading" v-show="loadingFlag">
      <img src="../../assets/img/loading.gif" alt="" >
    </div>
    <div v-show="noticeFlag" class="notice">
      <h4>已经没有更多数据</h4>
    </div>
  </div>
</template>

<script>
  import Axios from 'axios'


  export default {
    data(){
      return {
        movieList: [],
        loadingFlag: true,
        noticeFlag: false
      }
    },
    mounted(){
      this.loadData();

      //滚动条监听事件
      window.onscroll = ()=>{
        let cH = document.documentElement.clientHeight;
        let sT = document.documentElement.scrollTop;
        let sH = document.documentElement.scrollHeight;
        // console.log(cH,sT,sH);
        if(cH + sT == sH){
          // console.log("到底了!");
          if(!this.noticeFlag)
          {
          this.loadData();
            this.loadingFlag = true ;

          }

        }
      }


    },
    methods: {
      loadData(){
        //发送ajax请求
        // let url1 = API_PROXY+"http://m.maoyan.com/movie/list.json?type=hot&limit=10&offset="+this.movieList.length;
        let url2 = "./static/movieData.json"; //这个用于api不好用的时候
        Axios.get(url2).then((res)=>{
          let list1 =  res.data.data.movies;

          let data = list1.slice(this.movieList.length,this.movieList.length+10);
          if(data.length < 10)
          {
            this.noticeFlag = true;
          }
          let list2 = this.movieList.concat(data);     //这个用于api不好用的时候,配合url2使用

          this.loadingFlag = false;
          this.movieList = this.movieList.concat(list2);
        }).catch(()=>{
          alert("数据获取失败！");
        });
      },
      goDetail(id){
        this.$router.push('/movie/movieDetail/'+id);
      }
    }
}
</script>

<style scoped>

  .movie {
    display: flex;
    padding: 0.2rem;
    border-bottom: 1px solid #000;

  }
  .movie-list{
    margin: 1rem 0;
  }
  .movie-img{
    flex-grow: 1;
    width: 0;
    margin-right: 0.2rem;
  }
  .movie-img img{
    width: 100%;
  }
  .movie-name{
    font-weight: bolder;
  }
  .movie-info{
    flex-grow: 2;
    width: 0;
    margin-right: ;
  }
  .loading {
    width: 100%;
    position: relative;
    height: 85px;
  }
  .loading img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  .notice {
    text-align: center;
  }


</style>



