<template>
  <div class="albums">
    <a-player v-if="isShow" :music="songs[0]" :list="songs" showLrc></a-player>
  </div>
</template>

<script>
  import VueAplayer from 'vue-aplayer';
  import Axios from 'axios';

  export default {
      data(){
        return {
          songs: [],
          isShow: false
        }
      },
      components: {
        'a-player': VueAplayer
      },
    mounted(){
        Axios.get('./static/musicData.json').then(res=>{
          let list = res.data.musicData;
          list.forEach(ele=>{
            let obj = {
              title: ele.title,
              artist: ele.author,
              src: ele.src,
              pic: ele.musicImgSrc,
              lrc: ele.lrc
            }
            this.songs.push(obj);
          })
          this.isShow = true;
        }
    ).catch()
      console.log(this.songs);
    }
    }
</script>

<style scoped>
  .albums{
    margin: 1rem 0;
  }
</style>



