<template>
  <div class="header" :style="{background:bg}">
    <span class="home" @click="goHome">首页</span>
    <h2 class="title">
      <slot name="title">默认值</slot>
    </h2>
  </div>
</template>

<script>
    export default {
        methods: {
          goHome(){
            this.$router.push('/movie/movieList')
          }
        },
      props: ['bg']
    }
</script>

<style scoped>
  .header {
    height: 1rem;
    position: fixed;
    top: 0;
    background: #00BFFF;
    width: 100%;
    line-height: 1rem;
    color:#fff
  }
  .home {
    margin-left: 10px;
  }
  .title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
</style>



