<template>
  <div class="book">
    <common-header bg="rgb(0,100,0)">
      <span slot="title">Book</span>
    </common-header>
    <swipe class="my-swipe">
      <swipe-item class="slide1"></swipe-item>
      <swipe-item class="slide2"></swipe-item>
      <swipe-item class="slide3"></swipe-item>
    </swipe>
    <common-footer  bg="rgb(0,100,0)"></common-footer>
  </div>
</template>

<script>
  import CommonHeader from '../common/commonheader'
  import CommonFooter from '../common/commonfooter'
  require('vue-swipe/dist/vue-swipe.css');
  import { Swipe, SwipeItem } from 'vue-swipe';

  export default {
    components: {
      CommonFooter,
      CommonHeader,
      'swipe': Swipe,
      'swipe-item': SwipeItem
    }
  }
</script>

<style scoped>
  .book {
    margin: 1rem 0;
  }
  .my-swipe {
    height: 400px;
    color: #fff;
    font-size: 30px;
    text-align: center;
  }

  .slide1 {
    background: url("../../../static/img/7.jpg");
    background-size:100% 100% ;
    color: #fff;
  }

  .slide2 {
    background: url("../../../static/img/8.jpg");
    background-size:100% 100% ;
    color: #000;
  }

  .slide3 {
    background: url("../../../static/img/9.jpg") ;
    background-size:100% 100% ;
    color: #fff;
  }
</style>



