<template>
  <v-touch class="detail" :style="styleObj" @swipeleft="changeLeft" @swiperight="changeRight" @tap="tap">

  </v-touch>
</template>

<script>
    export default {
      data(){
        return {
          index: 0,

        }
      },
      mounted (){
        this.index = this.$route.params.index;
        },
      computed: {
        styleObj(){
          return {
            background: `#000 center/contain url(${this.$store.state.photoList[this.index].src}) no-repeat`
          }
        }
      },
      methods: {
        changeLeft(){
          this.index++;
          if(this.index == (this.$store.state.photoList.length))
          {
            this.index = 0;
          }
        },
        changeRight(){
          this.index--;
          if(this.index == -1)
          {
            this.index = this.$store.state.photoList.length - 1;
          }
        },

        tap(){
          this.$router.go(-1)
        }
      }
    }
</script>

<style scoped>
  .detail {
    margin: 1rem 0;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
</style>



