<template>
  <div class="detail">

      <div class="detail-content">
        <div class="detail-img">
          <img :src="detail.img" alt="">
        </div>
        <div class="detail-info">
          <p>{{detail.nm}}</p>
          <p>评分:{{detail.sc}}</p>
          <p>{{detail.cat}}</p>
          <p>{{detail.src}} / {{detail.dur}}分钟</p>
          <p>{{detail.rt}}</p>
        </div>
      </div>
    <hr>
      <div>
        <h3>主演:</h3>
        <p>{{detail.star}}</p>
        <hr>
        <h3>简介</h3>
        <p v-html="detail.dra"></p>

      </div>

  </div>
</template>

<script>
  import Axios from 'axios';

    export default {
      data(){
        return {
          detail: {

          }
        }
      },
      mounted(){
        let url1 = API_PROXY + 'http://m.maoyan.com/movie/'+this.$route.params.movieId+'.json'
        let url2 = './static/movieDetail.json'
        Axios.get(url2).then((res)=>{
          this.detail = res.data.data.MovieDetailModel;

        }).catch(()=>{

        })
      }
    }
</script>

<style scoped>
.detail {
  margin-top: 1rem;
  text-align: center;
}
  .detail-content {
    display: flex;
  }
  .detail-img{
    flex-grow: 1;
    width: 0;
  }
  .detail-img img {
    width: 100%;
  }
  .detail-info{
    flex-grow: 2;
    width: 0;
    margin-left: 0.2rem;
  }
  .detail-info p {
    height: 30px;
  }
</style>



