<template>
    <ul class="photo">
      <li v-for="(photo,index) in photoList" :key="index">
        <router-link :to="'/photo/photoDetail/'+index">
          <img :src="photo.src" alt="">
        </router-link>

      </li>
    </ul>
</template>

<script>
  import Axios from 'axios'
    export default {
    data(){
      return{
        photoList: []
      }
    },
      mounted(){
        Axios.get("./static/photoData.json").then(res=>{
            this.photoList = res.data.photoData;
            this.$store.dispatch('setPhotoList',this.photoList)
        }).catch()
      }
    }
</script>

<style scoped>
  @import url('../../assets/css/common.css');
  .photo {
    margin: 1rem 0;
  }
  ul {
    display: flex;
    flex-flow: wrap;
  }
  ul li {
    width: 50%;
  }
  img {
    width: 100%;
    height: 120px;
  }
</style>



